.container {
    position: relative;
    text-align: center;
    width: 100%;
}

.container img {
    margin: 0 auto;
    height: auto;
}

.container Button{
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
}